html {
  background: #fff;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  text-rendering: optimizelegibility;
  font-size: 100px;
  width: 100%;
  height: 100%;
}
body,
dl,
dt,
dd,
ul,
ol,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
legend,
input,
textarea,
p,
blockquote,
th,
td,
hr,
button,
article,
aside,
details,
figcaption,
figure,
footer,
header {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
}
a,
li,
button,
div {
  -webkit-tap-highlight-color: transparent;
}
body {
  width: 100%;
  font: 0.16rem PingFangSC-Regular PingFangSC-Medium, PingFang SC, "微软雅黑", Lantinghei SC, Hiragino Sans GB, Microsoft Sans Serif, WenQuanYi Micro Hei, sans;
  height: 100%;
  background: #FFFFFF !important;
}
#root {
  width: 100%;
  height: 100%;
}
#root > div {
  width: 100%;
  height: 100%;
}
#root .am-flexbox-item {
  background: #fff;
}
ul,
ol {
  list-style: none;
}
a,
img {
  display: inline-block;
}
.left {
  float: left;
  display: inline-block;
}
.right {
  float: right;
  display: inline-block;
}
.clear {
  clear: both;
}
.is-center {
  text-align: center;
}
.is-left {
  text-align: left;
}
.is-right {
  text-align: right;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0;
}
